:root {
  --border-radius: 5px;
}

.score-panel {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  border-radius: var(--border-radius);
  background-color: #ffa60083;
  font-weight: bold;
}

.panel-title {
  color: black;
  background-color: #ffa500;
  width: 100%;
  border-top-left-radius: var(--border-radius);
  border-top-right-radius: var(--border-radius);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 0.5em;
}

.panel-score {
  width: 100%;
  color: black;
}

.score-diff {
  position: absolute;
  margin-top: 1em;
  visibility: hidden;
  -webkit-animation: Move-up-disappear 500ms;
          animation: Move-up-disappear 500ms;
  color: grey;
}
@-webkit-keyframes Move-up-disappear {
  from {
    visibility: visible
  }
  to {
    -webkit-transform: translateY(-1em);
            transform: translateY(-1em)
  }
}
@keyframes Move-up-disappear {
  from {
    visibility: visible
  }
  to {
    -webkit-transform: translateY(-1em);
            transform: translateY(-1em)
  }
}



